<template>
  <div class="container-fluid container">
    <top-nav></top-nav>
    <bottom-nav></bottom-nav>
    <bot-info :infoMode="infoMode"></bot-info>
  </div>
</template>

<script>
import TopNav from './TopNav';
import BottomNav from './BottomNav';
import BotInfo from './BotInfo';

export default {
  name: 'Dashboard',
  components: {
    TopNav,
    BottomNav,
    BotInfo,
  },

  data() {
    return {
      infoMode: '',
    }
  },
}
</script>

<style>
.container {
  padding-top: 50px;
  padding-bottom: 50px;
}
</style>



